//------------------------------------------------------------------------------
//  Типографика
//------------------------------------------------------------------------------

html {
  font-size: @font-size-html;

  & when not (@font-size-html--sm = @font-size-html) {
    @media (min-width: @screen-sm) {
      font-size: @font-size-html--sm;
    }
  }
  & when not (@font-size-html--md = @font-size-html) {
    @media (min-width: @screen-md) {
      font-size: @font-size-html--md;
    }
  }
  & when not (@font-size-html--lg = @font-size-html) {
    @media (min-width: @screen-lg) {
      font-size: @font-size-html--lg;
    }
  }
  & when not (@font-size-html--xl = @font-size-html) {
    @media (min-width: @screen-xl) {
      font-size: @font-size-html--xl;
    }
  }
  & when not (@font-size-html--xxl = @font-size-html) {
    @media (min-width: @screen-xxl) {
      font-size: @font-size-html--xxl;
    }
  }
}

body {
  font-family: @font-family;
  font-size:   @font-size;
  line-height: @line-height;
  color:       @text-color;
}

// .h1, .h2, .h3, .h4, .h5, .h6,
h1, h2, h3, h4, h5, h6 {
  margin-top:    @headings__margin-top;
  margin-bottom: @headings__margin-bottom;
  font-family:   @headings__font-family;
  font-weight:   @headings__font-weight;
  line-height:   @headings__line-height;
  color:         @headings__color;
}

h1 { font-size: @font-size--h1; }
h2 { font-size: @font-size--h2; }
h3 { font-size: @font-size--h3; }
h4 { font-size: @font-size--h4; }
h5 { font-size: @font-size--h5; }
h6 { font-size: @font-size--h6; }


p {
  line-height: 1.6em;
  margin-top: 0;
  margin-bottom: @type-spacer;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 0;
}


ul ul {
   margin-bottom: 0;
}

