.toggler {
  position: absolute;
  z-index: 5;
  top: 0;
  right: 5px;
  display: flex;
  align-items: center;
  width: 2em;
  height: 3em;
  line-height: 3em;
  user-select: none;
}
.toggler > span {
  display: inline-block;
  vertical-align: bottom;
  width: 100%;
  height: .3rem;
  background: #fff;
  border-radius: .2rem;
  position: relative;
  transition: background .3s .5s;
}
.toggler > span:before,
.toggler > span:after {
  width: 100%;
  height: .3rem;
  background: #fff;
  border-radius: .2rem;
  position: absolute;
  left: 0;
  content: '';
  transform-origin: 50% 50%;
  transition: top .3s .6s, transform .3s;
}
.toggler > span:before {
  top: 1rem;
}
.toggler > span:after {
  top: -1rem;
}
.toggler--close > span {
  transition: background .3s 0;
  background: transparent;
}
.toggler--close > span:before,
.toggler--close > span:after {
  transition: top .3s, transform .3s .5s;
  top: 0;
}
.toggler--close > span:before {
  transform: rotate3d(0, 0, 1, 45deg);
}
.toggler--close > span:after {
  transform: rotate3d(0, 0, 1, -45deg);
}
@media (min-width: 768px) {
  .toggler {
    display: none;
  }
}
